"use client";

import { useState } from "react";
import { Calculator, CheckCircle } from "lucide-react";

const PayCalculator = () => {
  const [grossIncome, setGrossIncome] = useState(0);
  const [incomePeriod, setIncomePeriod] = useState("monthly");
  const [superannuation, setSuperannuation] = useState(true);
  const [helpDebt, setHelpDebt] = useState(true);
  const [nonResident, setNonResident] = useState(false);
  const [netIncome, setNetIncome] = useState(0);

  const calculateNetIncome = () => {
    let taxRate = 0.3; // Assume 30% default tax
    if (incomePeriod === "annually") taxRate = 0.3;
    if (incomePeriod === "monthly") taxRate = 0.25;
    if (incomePeriod === "fortnightly") taxRate = 0.2;
    if (incomePeriod === "weekly") taxRate = 0.15;

    let deductions = 0;
    if (superannuation) deductions += 0.05;
    if (helpDebt) deductions += 0.02;
    if (nonResident) taxRate += 0.05;

    const net = grossIncome * (1 - taxRate - deductions);
    setNetIncome(net);
  };

  return (
    <div className="max-w-4xl mx-auto p-6 bg-orange-100 rounded-lg shadow-lg">
      <h2 className="text-2xl font-bold text-gray-800">Welcome To Pay Calculator</h2>
      <p className="text-gray-600 mt-2">
        Enter your gross income, select the income period, and choose deductions.
      </p>

      {/* Income Input */}
      <div className="mt-6">
        <label className="block text-gray-700 font-semibold">Gross Income (Before Tax)</label>
        <input
          type="number"
          className="w-full p-3 mt-2 border rounded-lg"
          placeholder="$0.00"
          value={grossIncome}
          onChange={(e) => setGrossIncome(Number(e.target.value))}
        />
      </div>

      {/* Income Period Selection */}
      <div className="mt-4 flex space-x-3">
        {["annually", "monthly", "fortnightly", "weekly"].map((period) => (
          <button
            key={period}
            onClick={() => setIncomePeriod(period)}
            className={`px-4 py-2 rounded-lg ${
              incomePeriod === period ? "bg-orange-500 text-white" : "bg-gray-200"
            }`}
          >
            {period.toUpperCase()}
          </button>
        ))}
      </div>

      {/* Toggle Options */}
      <div className="mt-6 space-y-4">
        {[
          { label: "Includes Superannuation", state: superannuation, setState: setSuperannuation },
          { label: "Includes HELP Debt", state: helpDebt, setState: setHelpDebt },
          { label: "Non-Resident", state: nonResident, setState: setNonResident },
        ].map((item, index) => (
          <div key={index} className="flex justify-between items-center">
            <span className="text-gray-700">{item.label}</span>
            <div className="flex space-x-2">
              <button
                className={`px-4 py-1 rounded-lg ${
                  item.state ? "bg-orange-500 text-white" : "bg-gray-200"
                }`}
                onClick={() => item.setState(true)}
              >
                YES
              </button>
              <button
                className={`px-4 py-1 rounded-lg ${
                  !item.state ? "bg-orange-500 text-white" : "bg-gray-200"
                }`}
                onClick={() => item.setState(false)}
              >
                NO
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Calculate Button */}
      <button
        onClick={calculateNetIncome}
        className="mt-6 w-full bg-orange-500 text-white p-3 rounded-lg flex items-center justify-center space-x-2"
      >
        <Calculator className="w-5 h-5" />
        <span>CALCULATE</span>
      </button>

      {/* Net Income Display */}
      <div className="mt-6 p-4 bg-white rounded-lg shadow">
        <h3 className="text-xl font-semibold">Net Income:</h3>
        <p className="text-2xl font-bold text-green-600">${netIncome.toFixed(2)}</p>
      </div>
    </div>
  );
};

export default PayCalculator;
